.fitness-container {
  display: grid;
  grid-template-columns: 4fr 1fr;
  background-color: white;
}
.logo {
  width: 50px;
}
.profile-container {
  background: #95a0a7;
}
@media only screen and (max-width: 776px) {
  .fitness-container {
    display: flex;
    flex-direction: column;
  }
}
